				"html": "<h2>Offline IDEs</h2>\n<p>There are several offline <a><strong>IDE</strong></a> that programmers use in order to stay more productive to learn and be more productive at work.</p>\n<ul>\n<li><a href=\"https://www.app5.unisys.com/offerings/ClearPathConnection/eclipse.html\"><strong>Eclipse</strong></a> is more than a Java editor. Its biggest advantages are code completion by tabbing method which saves a lot of time when writing documentation.</li>\n</ul>\n<p>It has a built-in syntax check to fix any mistype words while coding a project. All of features we've come to expect of an IDE are their code completion, templates, integration with various SCMSs, and integration with build systems.</p>\n<p>Its code formatting and cleanup tools are very powerful. I find its build system to work well and intuitively. I think these are the things upon which its reputation is really built.</p>\n<p>Furthermore it has a refactoring that find and replace function, variables and classes of a specific work.</p>\n<ul>\n<li><a href=\"https://netbeans.org/features/\"><strong>NetBeans</strong></a> is a free intuitive editor that does it all compared from other IDEs. It has simple Swing GUI design tool to build user interfaces by dragging and dropping components for instance buttons, textboxes.</li>\n</ul>\n<p>The <em>disadvantage</em> it is that with all its features it is slow to load and uses more memory than other IDE's.</p>\n<ul>\n<li>\n<p><a href=\"https://www.jetbrains.com/idea/\"><strong>IntelliJ IDEA</strong></a> it is the most efficient offline editor however it is not free. It is faster than most editors however it has numbers of problems. Some major advantages is using too much memory from system.</p>\n</li>\n<li>\n<p><a href=\"http://www.bluej.org/\"><strong>BlueJ</strong></a> is a simple editor mostly used to teach java and object oriented programming. The main advantage of the editor over other ones is that it does not need a main method to run the program.</p>\n</li>\n</ul>\n<h2>Online IDEs</h2>\n<ul>\n<li><a href=\"http://www.webappers.com/2009/12/04/coderun-studio-free-cross-platform-online-ide/\"><strong>CodeRun Studio</strong></a> is an online IDE that you build web application. It allows you to run and test code online.</li>\n</ul>\n<p>The disadvantage is that there is a 14 days trial then it must be paid. It is difficult to save your project since you must pay in order to fully operate it.</p>\n<ul>\n<li><a href=\"https://c9.io\"><strong>Cloud9 IDE</strong></a> Cloud9 IDE is a open source, from version 3.0, online integrated development environment. It supports hundreds of programming languages, including <a><strong>PHP</strong></a>, <a><strong>Ruby</strong></a>, Perl, <a><strong>Python</strong></a>, <a href=\"http://forum.freecodecamp.com/t/advantages-and-disadvantages-of-javascript/14280\"><strong>JavaScript</strong></a> with Node.js, and <a><strong>Go</strong></a>. It enables developers to get started with coding immediately with pre-setup workspaces, collaborate with their peers with collaborative coding features, and web development features like live preview and browser compatibility testing.</li>\n</ul>\n<p>It is written almost entirely in JavaScript, and uses Node.js on the back-end. The editor component uses Ace. As of July 2014, it uses Docker containers for its workspaces, and is hosted on Google Compute Engine.</p>\n<p>Cloud9 IDE, Inc. is the company that actively maintains Cloud9 IDE. The company has offices in San Francisco and Amsterdam.</p>\n<h2>Conclusion</h2>\n<p>There are several differences between online editors and the offline editors.</p>\n<p>An offline IDE will be more beneficial to a programmer rather than an online IDE due to the fact that large projects will slow down, and a software team will need a reasonable office internet service.</p>\n<p>If there is a major storm the connection will be affected causing many projects to be unfinished and incomplete.</p>\n<p>The only advantage of an online IDE is that projects can be access everywhere; there are no setup and configurations. There is no hardware limitation since all information is be stored in the cloud.</p>",
